import React from "react";
import './verbalQuestionCard.scss';
import DeleteIcon from '@material-ui/icons/Delete';
import CreateIcon from "@mui/icons-material/Create";



export default function VerbalQuestionCard(props) {

    return <div className="verbal-question-card-main"
    onClick={props.onClick}
     style={{background:props.selected ? '#04724d' :
      props.publish ? '#fff' : '#f4f9f5',color:props.selected ? '#fff' : '#333',
      cursor:props.onClick ? 'pointer' : ''
      }}>
        <div className="verbal-text"    >{props.verbalText}</div>
        <div className="verbal-time-delete">
            <div className="verbal-time">{props.time}</div>
            {
                !props.publish && !props.isViewOnly && props.deleteHandler? <div className="verbal-delete" onClick={props.deleteHandler}>
                <DeleteIcon style={{height:'15px',width:'15px',color:'red'}}  />
                </div> : null
            }
            {
               !props.publish &&   !props.isViewOnly && props.editHandler ? <div className="verbal-edit" onClick={props.editHandler}>
                <CreateIcon style={{height:'15px',width:'15px',color: "#1473e6"}}  />
                </div> : null
            }
        </div>
     

    </div>

}